*{
  margin:0;
  padding:0;
}
body{
  background: url('https://img.freepik.com/free-vector/yellow-triangular-background_1409-1909.jpg');
  font-family: "Bree Serif",serif;
  display: flex;
  justify-content:center;
  align-items: center;
  min-height:100vh;
}
h1{
  text-align: center;
  color:#f71aa2;
}
.app{
  background: linear-gradient(5deg,#ff961d 0%,#f71aa2 100%);
  width:95%;
  min-height: 200px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0,0,0,0.75);
  display: flex;
  justify-content: space-evenly;
}
.Questions-Section{
  width: 100%;
  flex-direction: column;
  position: relative;
}
.Questions-Count{
  margin-bottom: 20px;
}
/* .Questions-Count span{
  font-size: 28px;
} */
.Question-text{
  margin-bottom: 12px;
  color:white;
  font-size: 25px;
}
.Answer-Section{
  width:100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.Answer-Section button{
  width:100%;
  font-size: 16px;
  color:#ffffff;
  background: linear-gradient(#ff961d 0%,#f71aa2 100%);
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border:3px solid #6d0ae6;
  cursor: pointer;
}
button:hover{
  background-color: rgb(242,186,46);
}
button:focus{
  outline: none;
}
.score-card{
  display:block;
  text-align: center;
  align-items:center;
  justify-content: center;
  color:green;
  font-size: 30px;
  font-weight: bold;
}
.score-card button{
  margin-right:73px;
  width:50%;
  margin-top:40px;
  font-size: 16px;
  color:#ffffff;
  justify-content: flex-start;
  background: linear-gradient(#ff961d 0%,#f71aa2 100%);
  border-radius: 15px;
  padding: 5px;
  align-items: center;
  border:3px solid #6d0ae6;
  cursor: pointer;
}